<template>
    <div class="raft_container">
        <h2>{{ phrase }}</h2>
        <div>
            <button class="button" v-on:click="startNotify" :disabled="disabled">{{start ? end ? 'end' : "running" : 'start'}}</button>
        </div>
        <div class="raft_body">
            <ul>
                <li v-for="(raft, index) in rafts" :key="index" :id="'raft' + index" class="raft">
                    <span :class="'raft_index' + ' ' + 'state' + raft.state + ' ' + 'connected' + raft.connected + ' ' + 'crash' + raft.crash + ' '">{{ raft.id }}</span>
                    <span v-for="(log, idx) in raft.logs" :key="idx" class="log">
                        <div class="log_item"><span>{{log.log_term}}</span></div>
                        <div class="log_item"><span>{{log.log_index}}</span></div>
                    </span>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'RaftView',
        data() {
            return {
                start: false,
                disabled: false,
                end: false,
                interval: {},
                phrase: "wait to start",
                rafts: []
            }
        },
        created() {
            clearInterval(this.interval)
        },
        methods: {
            async getRaftList() {
                const {data: res} = await this.$http.get('raft_view')
                this.rafts = res.nodes
                this.phrase = res.phrase
                if (res.end) {
                    this.end = res.end
                    clearInterval(this.interval)
                }
            },
            async startNotify() {
                this.disabled = true

                const {data: res} = await this.$http.get("raft_view", {
                    params: {
                        start: true,
                    }
                }).finally(() => {
                    this.disabled = true
                })
                this.start = true
                this.rafts = res.nodes
                this.phrase = res.phrase
                this.interval = setInterval(this.getRaftList, 100)
            }
        },
        destroyed() {
            clearInterval(this.interval)
        }
    }
</script>

